  .offset-by-two-thirds.columns {
    margin-left: 69.33333%;
  }

  .offset-by-one-half.column,
  .offset-by-one-half.column {
    margin-left: 52%;
  }
}
.clearfix:after,
.clearfix:before {
  /* 1 */
  content: " ";
  /* 2 */
  display: table;
}

.clearfix:after {
  clear: both;
}

.container:after,
.row:after,
.u-cf {
  content: "";
  display: table;
  clear: both;
}

.navbar {
  width: 100%;
  background: red;
}
.navbar__wrap {
  max-width: 100%;
  margin: 0 2.4rem;
  position: relative;
}
.navbar__logo {
  display: inline-block;
  height: 55px;
  line-height: 55px;
  float: left;
}
.navbar__logo > img {
  height: 35px;
}
@media only screen and (max-width: 768px) {
  .navbar__nav {
    width: 100%;
    position: absolute;
    top: 100%;
    left: 0;
    overflow-y: hidden;
    background: rgba(16, 16, 16, 0.97);
  }
}
@media only screen and (min-width: 768px) {
  .navbar__nav {
    float: right;
  }
}
.navbar__item {
  display: block;
  height: 37.5px;
  line-height: 37.5px;
}
@media only screen and (max-width: 768px) {
  .navbar__item {
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  }
}
@media only screen and (min-width: 768px) {
  .navbar__item {
    display: inline-block;
    height: 55px;
    line-height: 55px;
  }
}
.navbar__item--link {
  display: inherit;
  padding: 0 2.4rem;
  color: #fff;
}
@media only screen and (max-width: 768px) {
  .navbar__item--link:hover {
    background: rgba(16, 16, 16, 0.9);
    color: #c0a062;
  }
}
@media only screen and (min-width: 768px) {
  .navbar__item--link {
    color: #333;
  }
}

